import React from 'react';
import {AppThunk} from "./redux-store";
import {getAuthUserData} from "./auth-reducer";
import {getProfileThunkCreator} from "./profile-reducer";

export const initialState = {
    initialized: false
}
export type AppInitialType = typeof initialState

const AppReducer = (state: AppInitialType = initialState, action: AppMainType) => {
    switch (action.type) {
        case 'SET_INITIALISED':
            return {...state, initialized: true}
        default:
            return state
    }
};

export type AppMainType = initializedSuccessType
type initializedSuccessType = ReturnType<typeof initializedSuccess>

export const initializedSuccess = () => ({type: 'SET_INITIALISED'} as const)

export const initializeApp = (): AppThunk => async (dispatch, getState) => {
    await dispatch(getAuthUserData())
    const userId = getState().auth.id
    await dispatch(getProfileThunkCreator(userId))
    dispatch(initializedSuccess())
}

export default AppReducer;